import { useTranslation } from "react-i18next";
import CustomSelectCashier from "../../../Ui/Select/CustomSelectCashier";
import { useFinanceCashierStaffList } from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";

const CashierDorpdown = ({
  instituteId,
  onSelect,
  errorShow,
  defalutValue,
  isDenied,
  customStyleContainer,
}) => {
  const { t } = useTranslation();
  const { financeCashierStaffList } = useFinanceCashierStaffList({
    data: {
      id: instituteId,
    },
    skip: !instituteId,
  });
  return (
    <>
      <CustomSelectCashier
        selectLabel={t("cashier")}
        selectedValue={t("select_cashier_placeholder")}
        options={
          financeCashierStaffList?.length > 0 ? financeCashierStaffList : []
        }
        onClick={onSelect}
        isFull
        errorShow={errorShow}
        defalutValue={defalutValue}
        isDenied={isDenied}
        customStyleContainer={customStyleContainer}
      />
    </>
  );
};

export default CashierDorpdown;
