import { useState } from "react";
import FormCalendarInput from "../../../Ui/Input/FormCalendarInput";
import CustomSelect from "../../../Ui/Select/CustomSelect";
import FeeStatisticsBankFilter from "../FeeStatistics/FeeStatisticsBankFilter";
import {
  slashDateFormat,
  ymdSlashDateFormat,
} from "../../../Utils/Functions/dateUtility";
import { useTranslation } from "react-i18next";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import style from "../FinanceManager.module.css";
import CreateButton from "../../../Ui/Button/CreateButton";
import {
  useFinanceDaybookCombinedFilterGenerate,
  useFinanceDaybookCombinedSummaryFilterGenerate,
  useFinanceDaybookCombinedSummaryDetailFilterGenerate,
  useFinanceDaybookFilterGenerate,
} from "../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
import CustomGeneratedPdf from "../../../Ui/GeneratedPdf/CustomGeneratedPdf";
import { daybookBaseUrl } from "../../../Services/UrlConfig/BaseUrl";
import CashierDorpdown from "./CashierDorpdown";
import TileWrapper from "../../../Ui/Tab/Tile/TileWrapper";
import TileCard from "../../../Ui/Tab/Tile/TileCard";
const FinanceDaybookFilter = ({ fid, instituteId }) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    bank: "",
    from: "",
    to: "",
    payment_type: "",
    daybook_type: "",
    staff: "",
    cashier_type: "ALL",
    root_daybook_type: "",
  });
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");
  const [openPdf, setOpenPdf] = useState("");

  const [financeDaybookFilterGenerate] = useFinanceDaybookFilterGenerate();
  const [financeDaybookCombinedFilterGenerate] =
    useFinanceDaybookCombinedFilterGenerate();
  const [financeDaybookCombinedSummaryFilterGenerate] =
    useFinanceDaybookCombinedSummaryFilterGenerate();
  const [financeDaybookCombinedSummaryDetailFilterGenerate] =
    useFinanceDaybookCombinedSummaryDetailFilterGenerate();
  const onTransactionDate = (val, flow) => {
    setState((prev) => ({
      ...prev,
      [flow]: ymdSlashDateFormat(val),
    }));
  };
  const onSelectBank = (value) => {
    setState((pre) => ({
      ...pre,
      bank: value,
    }));
  };
  const onSelectType = (val) => {
    setState((prev) => ({
      ...prev,
      payment_type:
        val === "Total" ? "BOTH" : val === "Cash / Bank" ? "CASH_BANK" : val,
    }));
  };
  const onSelectRootDaybookType = (val) => {
    setState((prev) => ({
      ...prev,
      root_daybook_type: val,
    }));
  };
  const onSelectDaybookType = (val) => {
    setState((prev) => ({
      ...prev,
      daybook_type: val,
      // payment_type: "",
    }));
  };

  const onSelectCashier = (val) => {
    setState((prev) => ({
      ...prev,
      staff: val,
    }));
  };

  const onCashierType = (val) => {
    if (val === "ALL") {
      setState((prev) => ({
        ...prev,
        cashier_type: val,
        staff: "",
      }));
    } else {
      setState((prev) => ({
        ...prev,
        cashier_type: val,
        staff: "",
      }));
    }
  };

  const onFilter = () => {
    let dft = [];
    if (
      state.root_daybook_type === "Summary Daybook" ||
      state.root_daybook_type === "Detail Daybook"
    ) {
      dft = ["staff", "cashier_type", "daybook_type"];
    } else {
      dft = ["staff", "cashier_type"];
    }
    let validation = requiredField(state, dft);
    if (existingRequiredField(validation) && fid && state.bank?._id) {
      setDisabled((pre) => !pre);
      let whichFlow = "MISCELLENOUS";
      let hostelId = null;
      if (state.bank?.hostel?._id) {
        whichFlow = "HOSTEL";
        hostelId = state.bank?.hostel?._id;
      } else {
        if (state.daybook_type === "Admission") whichFlow = "ADMISSION";
      }
      if (state.root_daybook_type === "Summary Daybook") {
        financeDaybookCombinedSummaryFilterGenerate({
          fid: fid,
          bodyParameter: {
            bank: state.bank?._id,
            from: state.from,
            to: state.to,
            payment_type:
              state.payment_type === "BOTH"
                ? "Total"
                : state.payment_type === "CASH_BANK"
                ? "Cash / Bank"
                : state.payment_type,
            staff: state.staff?._id ?? "",
          },
        })
          .then((res) => {
            if (res?.data?.key) {
              setTimeout(() => {
                setOpenPdf(res?.data?.key);
                setDisabled((pre) => !pre);
              }, 3000);
            }
          })
          .catch({});
      } else if (state.root_daybook_type === "Detail Daybook") {
        financeDaybookCombinedSummaryDetailFilterGenerate({
          fid: fid,
          bodyParameter: {
            bank: state.bank?._id,
            from: state.from,
            to: state.to,
            payment_type:
              state.payment_type === "BOTH"
                ? "Total"
                : state.payment_type === "CASH_BANK"
                ? "Cash / Bank"
                : state.payment_type,
            staff: state.staff?._id ?? "",
          },
        })
          .then((res) => {
            if (res?.data?.key) {
              setTimeout(() => {
                setOpenPdf(res?.data?.key);
                setDisabled((pre) => !pre);
              }, 3000);
            }
          })
          .catch({});
      } else {
        if (state.daybook_type === "Combined") {
          financeDaybookCombinedFilterGenerate({
            fid: fid,
            bodyParameter: {
              bank: state.bank?._id,
              from: state.from,
              to: state.to,
              payment_type:
                state.payment_type === "BOTH"
                  ? "Total"
                  : state.payment_type === "CASH_BANK"
                  ? "Cash / Bank"
                  : state.payment_type,
              staff: state.staff?._id ?? "",
            },
          })
            .then((res) => {
              if (res?.data?.key) {
                setTimeout(() => {
                  setOpenPdf(res?.data?.key);
                  setDisabled((pre) => !pre);
                }, 3000);
              }
            })
            .catch({});
        } else {
          financeDaybookFilterGenerate({
            fid: fid,
            bank: state.bank?._id,
            from: state.from,
            to: state.to,
            payment_type: state.payment_type,
            flow: whichFlow,
            hid: hostelId,
            staff: state.staff?._id ?? "",
          })
            .then((res) => {
              if (res?.data?.key) {
                setTimeout(() => {
                  setOpenPdf(res?.data?.key);
                  setDisabled((pre) => !pre);
                }, 3000);
              }
            })
            .catch({});
        }
      }
    } else {
      setErrorField(validation);
    }
  };
  // console.info("sdfjgvdfs", openPdf);
  return (
    <>
      <div
        className={style.fm_header_start_container}
        style={{
          gap: "2%",
          flexWrap: "wrap",
        }}
      >
        <CustomSelect
          selectLabel={t("daybook_type")}
          selectedValue={t("daybook_type_placeholder")}
          options={["Normal Daybook", "Summary Daybook", "Detail Daybook"]}
          onClick={onSelectRootDaybookType}
          defalutValue={state.root_daybook_type}
          customStyleContainer={{
            width: "32%",
          }}
          customListStyle={{
            position: "absolute",
            width: "100%",
            zIndex: "13",
            top: "5.4rem",
          }}
          errorShow={errorField["root_daybook_type"]}
        />
        <FeeStatisticsBankFilter
          fid={fid}
          onSelect={onSelectBank}
          errorShow={errorField["bank"]}
          viewAs="NORMAL_FILTER"
          defalutValue={state?.bank?.finance_bank_account_name ?? ""}
          customStyleContainer={{
            width: "32%",
          }}
          customListStyle={{
            position: "absolute",
            width: "100%",
            zIndex: "13",
            top: "5.4rem",
          }}
        />
        {state.root_daybook_type === "Summary Daybook" ||
        state.root_daybook_type === "Detail Daybook" ? null : (
          <CustomSelect
            selectLabel={t("fee_type")}
            selectedValue={t("fee_type_placeholder")}
            options={[
              "Admission",
              "Miscellaneous",
              "Combined",
              // "Summary Daybook",
            ]}
            onClick={onSelectDaybookType}
            defalutValue={state.daybook_type}
            customStyleContainer={{
              width: "32%",
            }}
            customListStyle={{
              position: "absolute",
              width: "100%",
              zIndex: "13",
              top: "5.4rem",
            }}
            errorShow={errorField["daybook_type"]}
          />
        )}

        <FormCalendarInput
          label={t("from")}
          placeholder={t("please_select_from_date")}
          name={"from"}
          value={state.from ? slashDateFormat(state.from) : ""}
          onChange={(e) => onTransactionDate(e, "from")}
          errorShow={errorField["from"]}
          customFormInputContainer={{
            width: "32%",
          }}
        />
        <FormCalendarInput
          label={t("to_only")}
          placeholder={t("please_select_to_date")}
          name={"to"}
          value={state.to ? slashDateFormat(state.to) : ""}
          onChange={(e) => onTransactionDate(e, "to")}
          errorShow={errorField["to"]}
          customFormInputContainer={{
            width: "32%",
          }}
        />
        {state.daybook_type === "Miscellaneous" ? (
          <CustomSelect
            selectLabel={t("payment_type")}
            selectedValue={t("please_select_payment_type")}
            options={["Payment Gateway - PG", "By Cash"]}
            onClick={onSelectType}
            defalutValue={state.payment_type}
            customStyleContainer={{
              width: "32%",
            }}
            customListStyle={{
              position: "absolute",
              width: "100%",
              zIndex: "13",
              top: "5.4rem",
            }}
            errorShow={errorField["payment_type"]}
          />
        ) : (
          <CustomSelect
            selectLabel={t("payment_type")}
            selectedValue={t("please_select_payment_type")}
            options={[
              "Payment Gateway / Online",
              "By Cash",
              "Cash / Bank",
              "Total",
            ]}
            onClick={onSelectType}
            defalutValue={
              state.payment_type === "BOTH"
                ? "Total"
                : state.payment_type === "CASH_BANK"
                ? "Cash / Bank"
                : state.payment_type
            }
            customStyleContainer={{
              width: "32%",
            }}
            customListStyle={{
              position: "absolute",
              width: "100%",
              zIndex: "13",
              top: "5.4rem",
            }}
            errorShow={errorField["payment_type"]}
          />
        )}
        <TileWrapper
          label="cashier_type"
          customTileStyle={{
            width: "49%",
          }}
        >
          {cashierLabel?.map((tile) => (
            <TileCard
              key={tile?.key}
              tile={tile}
              onTile={onCashierType}
              activeTile={state.cashier_type}
            />
          ))}
        </TileWrapper>
        {state.cashier_type === "PARTICULAR" ? (
          <CashierDorpdown
            onSelect={onSelectCashier}
            instituteId={instituteId}
            defalutValue={
              state?.staff
                ? `${state?.staff?.staffFirstName ?? ""} ${
                    state?.staff?.staffMiddleName ?? ""
                  } ${state?.staff?.staffLastName ?? ""}`
                : ""
            }
            customStyleContainer={{
              width: "49%",
            }}
          />
        ) : null}
      </div>
      <CreateButton label="view_report" onAction={onFilter} />
      {disabled && <QvipleLoader />}
      {openPdf && (
        <CustomGeneratedPdf
          subUrl={daybookBaseUrl}
          documentUrl={openPdf}
          fileName={openPdf}
          onClose={() => setOpenPdf("")}
        />
      )}
    </>
  );
};

export default FinanceDaybookFilter;
export const cashierLabel = [
  {
    key: "ALL",
    name: "all",
  },
  {
    key: "PARTICULAR",
    name: "particular_cashier",
  },
];
